import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { PageNotFoundComponent } from './pagenotfound/pagenotfound.component';

const routes: Routes = [
  {
    path: '',
    redirectTo: 'result',
    pathMatch: 'full'
  },
  {
    path: 'result',
    loadChildren: () => import('./result/result.module').then(m => m.ResultComponentModule)
  },
  {
    path: 'last30',
    loadChildren: () => import('./last30/last30.module').then(m => m.Last30ComponentModule)
  },
  {
    path: 'last30/detail',
    loadChildren: () => import('./detail/detail.module').then(m => m.DetailComponentModule)
  },
  {
    path: 'topnum',
    loadChildren: () => import('./topnum/topnum.module').then(m => m.TopNumComponentModule)
  },
  {
    path: 'multical',
    loadChildren: () => import('./multical/multical.module').then(m => m.MultiCalComponentModule)
  },
  {
    path: 'bankercal',
    loadChildren: () => import('./bankercal/bankercal.module').then(m => m.BankerCalComponentModule)
  },
  {
    path: 'checkresult',
    loadChildren: () => import('./checkresult/checkresult.module').then(m => m.CheckResultComponentModule)
  },
  {
    path: 'checkresult/list',
    loadChildren: () => import('./checkresult/checkresult.module').then(m => m.CheckResultComponentModule)
  },
  {
    path: 'capcheck',
    loadChildren: () => import('./capcheck/capcheck.module').then(m => m.CapCheckComponentModule)
  },
  {
    path: 'capcheck/result',
    loadChildren: () => import('./capcheck/capcheck.module').then(m => m.CapCheckComponentModule)
  },
  {
    path: 'capcheck/list',
    loadChildren: () => import('./capcheck/capcheck.module').then(m => m.CapCheckComponentModule)
  },
  {
    path: 'about',
    loadChildren: () => import('./about/about.module').then(m => m.AboutComponentModule)
  },
  {
    path: '404',
    component: PageNotFoundComponent
  },
  {
      path: '**', redirectTo: '/404'
  },
];

@NgModule({
  imports: [RouterModule.forRoot(routes, { enableTracing: false, useHash: true, relativeLinkResolution: 'legacy' })],
  exports: [RouterModule]
})
export class AppRoutingModule {}
